import { barDefaultColor, sucessColor } from "../constants/Color";
import { v4 } from 'uuid';

const blink = (element: d3.Selection<any, unknown, null, undefined>) => {
  const repeat = () => {
    element
      .transition(v4())
      .duration(100)
      .attr('fill', sucessColor)
      .on('end', function () {
        element
        .transition(v4())
        .duration(100)
        .attr('fill', barDefaultColor)
        .on('end', function () {
          repeat();
        });
      });

  }
  
  repeat();
}

export { blink };