enum DataType {
  BAR = 50, MATRIX = 10, GRAPH = 10
}


const getInputNumElements = (dataType: DataType): number[] => {
  switch (dataType) {
    case DataType.BAR:
      return [10, 50, 100, 200];
    case DataType.MATRIX:
      return [5, 10, 20, 50];
  }
}

export { getInputNumElements };
  
export default DataType;